import React from 'react';

import {
  Panel,
  CardGrid,
  Card,
  Placeholder,
  Button,
  Avatar
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import bridge from '@vkontakte/vk-bridge';

export default class GetPhoneNumberPanel extends React.Component {
  
  getPhoneNumber = () => {
    if (bridge.supports("VKWebAppGetPhoneNumber")) {
      bridge.send("VKWebAppGetPhoneNumber", {}).then(res => {
        this.props.sendResponse({
          phone_number: res.phone_number
        });
      }).catch(e => {
        this.props.sendError("Не удалось получить номер телефона", e)
      });
    } else {
      this.props.sendError("Получение номера телефона на этом устройстве не доступно", {
        error_code: "not_supports"
      })
    };
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader/>
        <StandardRequestAccess 
          onClick={this.getPhoneNumber} 
          hashParams={hashParams} 
          requests="Запрашивает доступ к Вашему номеру телефона"
        />
      </Panel>
    );
  }
}