import React from 'react';

import {
  Panel
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';

import bridge from '@vkontakte/vk-bridge';

export default class DownloadFilePanel extends React.Component {
  
  componentWillMount () {
    let {hashParams} = this.props;
    if (bridge.supports("VKWebAppDownloadFile")) {
      bridge.send("VKWebAppDownloadFile", {
        url: hashParams.get('file_url'),
        filename: hashParams.get('file_name')
      }).then(res => {
        this.props.sendResponse(res)
      }).catch(e => {
        this.props.sendError("Не удалось скачать файл", e)
      })
    } else {
      this.props.sendError("Скачивание файла не доступно на этом устройстве", {
        error_code: "not_supports"
      })
    }
  }

  render () {
    const {id} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader/>
        Файл скачивается ...
      </Panel>
    );
  }
}