import React from 'react';

import {
  Panel,
  PanelHeader,
  Search,
  PanelSpinner,
  Div,
  Group,
  Header,
  List,
  Cell,
  Link
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';

import bridge from '@vkontakte/vk-bridge';

export default class GetCitiesPanel extends React.Component {
  constructor (props) {
    super(props);
    
    this.state = {
      countries: [],
      cities: [],
      selectedCountryId: 0,
      loadedCountries: false,
      loadedCities: false,
      countryName: ''
    }

  }

  componentDidMount () {
    let {userToken} = this.props;

    if (!this.state.countries.length) {
      this.loadCountries();
    }
  }

  async loadCountries (q="") {
    
    this.setState({
      loadCountries: false
    });

    let countries = await this.props.vkApi('database.getCountries', {
      need_all: 1,
      q,
      count: 1000
    });

    this.setState({
      loadedCountries: true,
      countries: countries.items
    })
  }

  async loadCities (q="") {
    this.setState({
      loadedCities: false
    });

    let cities = await this.props.vkApi('database.getCities', {
      country_id: this.state.selectedCountryId,
      q
    })

    this.setState({
      loadedCities: true,
      cities: cities.items
    })
  }

  selectCountry = (countryId) => {
    const {activeHash} = this.props;
    if (activeHash === "selectCountry") {
      this.props.sendResponse({
        "country_id": countryId,
        "country_title": this.state.countries.find(c => c.id === countryId).title
      });
    } else {
      this.setState({
        selectedCountryId: countryId
      }, () => {
        this.loadCities();
      });
    } 
  }

  selectCity = async (city) => {
    this.props.sendResponse({
      "city_id": city.id,
      "city_title": city.title,
      "country_id": this.state.selectedCountryId,
      "country_title": this.state.countries.find(c => c.id === this.state.selectedCountryId).title
    });

    this.clearCities();
  } 

  clearCities = () => {
    this.setState({
      cities: [],
      selectedCountryId: 0,
      countryName: ''
    })
  }

  renderCities () {
    const {cities, loadedCities} = this.state;
    return (
      !cities.length && !loadedCities ? <PanelSpinner size="l"/> :
      !cities.length ? <Div>Произошла неизвестная ошибка</Div> :
      <div>
        <Group header={<Header mode="secondary" aside={<Link onClick={this.clearCities}>Изменить страну</Link>}>Выберите город</Header>}>
          <List>
            {cities.map(city => (
              <Cell onClick={() => this.selectCity(city)} key={"city" + city.id}>
                {city.title}
              </Cell>
            ))}
          </List>
        </Group>
      </div>
    )

  }

  changeSearchQuery = (e) => {
    let q = e.target.value;
    if (this.state.selectedCountryId) {
      this.loadCities(q);
    } else {
      this.setState({
        countryName: String(q).toLocaleLowerCase()
      })
    }
  }

  render () {
    const {countries, loadedCountries, countryName, selectedCountryId} = this.state;
    return (
      <Panel id={this.props.id}>
        <StandardSearchHeader setSearch={true} key={selectedCountryId ? "country" : "city"} onChange={this.changeSearchQuery}/>
        {!countries.length && !loadedCountries ? <PanelSpinner size="l"/> : 
        !countries.length ? <Div>Произошла неизвестная ошибка</Div> : 
          !selectedCountryId ? <Group header={<Header mode="secondary">Выберите страну</Header>}>
            <List>
              {countries.filter(c => {
                return String(c.title).toLocaleLowerCase().match(countryName)
              }).map(country => (
                <Cell onClick={() => this.selectCountry(country.id)} key={"country" + country.id}>
                  {country.title}
                </Cell>
              ))}
            </List>
          </Group> : this.renderCities()
        }
      </Panel>
    );
  }
} 