import 'url-search-params-polyfill';

import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';

import {
	View,
	ScreenSpinner,
	PanelSpinner,
	Panel
} from '@vkontakte/vkui';


import '@vkontakte/vkui/dist/vkui.css';

import GetCitiesPanel from './panels/getCities';
import GettingAuthPanel from './panels/gettingAuth';
import NotFoundPanel from './panels/notFound';
import HelloPanel from './panels/hello';
import GetEmailPanel from './panels/getEmail';
import GetPhoneNumberPanel from './panels/getPhoneNumber';
import JoinGroupPanel from './panels/joinGroup';
import AllowMessagesFromGroupPanel from './panels/allowMessagesFromGroup';
import RouteDialogPanel from './panels/routeDialog';
import GetAppVersionPanel from './panels/getAppVersion';
import ReadQRCodePanel from './panels/readQRCode';
import DownloadFilePanel from './panels/downloadFile';

import './css/main.css';

export const APP_ID = 7388296;

class App extends React.Component {
	constructor (props) {
		super(props);

		this.queryString = window.location.search;

		this.queryParams = new URLSearchParams(this.queryString);
		console.log(this.queryString, this.queryParams)

		let supportsHashes = [
			'selectCity', 
			'selectCountry', 
			'getEmail', 
			'getPhoneNumber',
			'joinGroup',
			'allowMessagesFromGroup',
			'routeDialog',
			'getAppVersion',
			'readQRCode',
			'downloadFile'
		];

		let hashParams = window.location.hash.replace(/^#/, '?');
		console.log(hashParams)
		let activeHash = '';


		if (hashParams.replace('?', '').match(/^(\d+)/)) {
			hashParams = `?method=routeDialog&bot_id=${hashParams.replace('?', '')}`;
		}

		console.log(hashParams, 'hashParams 2')
		hashParams = new URLSearchParams(hashParams);
		
		this.hashParams = hashParams;

		activeHash = hashParams.get('method');
		console.log(activeHash)
		if (supportsHashes.indexOf(activeHash) === -1) {
			if (activeHash && activeHash.length) {
				activeHash = 'notFound';
			} else {
				activeHash = 'hello';
			}
		}

		let activePanel = 'gettingAuth';
		let alreadyAuth = this.queryParams.get('vk_access_token_settings') === "status";

		if (alreadyAuth || (['selectCountry', 'selectCity'].indexOf(activeHash) === -1)) {
			activePanel = activeHash;
		}

		console.log(activePanel, activeHash, alreadyAuth)

		this.state = {
			userToken: '',
			activePanel: activePanel,
			activeHash: activeHash,
			popout: null,
			hashParams
		}

		if (alreadyAuth) {
			this.requestToken();
		}	

		this.wasClosed = false;

		bridge.subscribe((e) => {
			if (e.type === "VKWebAppLocationChanged" || (e.type === "VKWebAppViewRestore" && this.wasClosed)) {
				window.location.reload();
			} else if (e.type === "VKWebAppViewHide") {
				this.wasClosed = true;
			}
		})
 	}

 	componentDidMount () {
 		if (this.state.activeHash === 'notFound') {
 			this.sendError("Метод не найден", {}, false)
 		}
 	}

 	sendResponse = async (response={}, close=true) => {
 		
 		let appPayload = {
 			method: this.state.activeHash,
 			hash: window.location.hash.replace('#', ''),
 			...response
 		}

 		if (!appPayload.error) appPayload.success = true;

 		let botId = this.hashParams.get('bot_id');

    if (botId) {
      await bridge.send("VKWebAppSendPayload", {
        "group_id": Number(botId), 
        "payload": appPayload
      });
    }

    if (close) {
    	
    	if (this.queryParams.get('vk_platform') === 'desktop_web' || this.queryParams.get('vk_platform') === 'mobile_web') {
    		window.history.back();
    	} else {
    		bridge.send("VKWebAppSetLocation", {"location": window.location.hash + '&' + new Date().getTime()});
    		await bridge.send("VKWebAppClose", {
		      "status": "success", 
		      "payload": appPayload
		    });
    	}
    }
 	}

 	sendError = async (message="", response={}, close=true) => {
 		return this.sendResponse({
 			...response,
 			error: true,
 			error_message: message
 		}, close)
 	}

	requestToken = async () => {

		bridge.send("VKWebAppGetAuthToken", {
			"app_id": APP_ID, 
			"scope": "status"
		}).then(token => {
			this.setState({
				userToken: token.access_token,
				activePanel: this.state.activePanel === 'gettingAuth' ? this.state.activeHash : this.state.activePanel
			});
		});
	}

	makeApiCall = async (method, params={}) => {
		const {userToken} = this.state;
		return bridge.send("VKWebAppCallAPIMethod", {
      "method": method, 
      "request_id": String(new Date().getTime()), 
      "params": {
      	"v": "5.103",
      	"access_token": userToken,
      	...params
      }
    }).then(res => res.response)
	}

	render () {
		let {activePanel} = this.state;
		const {popout, userToken, hashParams, activeHash} = this.state;
		
		if (activePanel !== "notFound" && activePanel !== "gettingAuth" && activePanel !== "hello" && !userToken
			&& ['selectCountry', 'selectCity'].indexOf(activeHash) !== -1) {
			activePanel = "loading";
		}

		if (activePanel === "selectCountry") {
			activePanel = "selectCity"
		}

		if (activePanel === "loading") {
			return (
				<PanelSpinner size="l"/>
			);
		}

		return (
			<View activePanel={activePanel} popout={popout}>
				<HelloPanel id="hello"/>
				<NotFoundPanel id="notFound"/>
				
				<GetEmailPanel id="getEmail" activeHash={this.state.activeHash} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				
				<ReadQRCodePanel id="readQRCode" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<DownloadFilePanel id="downloadFile" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				
				<RouteDialogPanel id="routeDialog" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<JoinGroupPanel id="joinGroup" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<GetAppVersionPanel id="getAppVersion" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<AllowMessagesFromGroupPanel id="allowMessagesFromGroup" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<GetPhoneNumberPanel id="getPhoneNumber" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<GetCitiesPanel id="selectCity" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} userToken={userToken} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<GettingAuthPanel id="gettingAuth" activeHash={this.state.activeHash} sendError={this.sendError} onRequestToken={this.requestToken}/>
			</View>
		);
	}
}

export default App;

