import React from 'react';

import {
  Panel,
  Div,
  Button,
  Card,
  CardGrid,
  Placeholder
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import Icon56HideOutline from '@vkontakte/icons/dist/56/hide_outline';

const renderAction = (type="") => {
  switch (type) {
    case 'selectCity':
      return 'выбрать город';
    default: 
      return 'пользоваться сервисом';
  }
}
export default ({onRequestToken, id, activeHash}) => (
  <Panel id={id} centered header={false}>
    <StandardSearchHeader title="ChatBot Tools"/>
    <Placeholder
      icon={<Icon56HideOutline/>}
      action={<Button size="xl" onClick={onRequestToken}>Разрешить</Button>}
    >Чтобы {renderAction(activeHash)}, вам нужно разрешить доступ к общедоступной информации о Вас.</Placeholder>
  </Panel>
)