import React from 'react';

import {
  Panel,
  Div,
  Link,
  Button,
  Placeholder
} from '@vkontakte/vkui'

import StandardSearchHeader from '../components/standardSearchHeader';

import bridge from '@vkontakte/vk-bridge';
import Icon24Services from '@vkontakte/icons/dist/24/services';
import Icon56MessageReadOutline from '@vkontakte/icons/dist/56/message_read_outline';

export default ({id}) => (
  <Panel id={id} centered>
    <StandardSearchHeader title="Привет!"/>
    <Placeholder
      icon={<Icon56MessageReadOutline/>}
      header="ChatBot Tools"
      action={<Button size="xl" onClick={() => {
          bridge.send("VKWebAppAddToCommunity", {});
        }} before={<Icon24Services/>}>Установить в сообщество</Button>}
    >Это новые возможности для Ваших <br/> чат-ботов ВКонтакте.</Placeholder>
  </Panel>
)