import React from 'react';

import {
  Panel,
  Link,
  Button,
  Gradient
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import bridge from '@vkontakte/vk-bridge';
import Icon56MessageReadOutline from '@vkontakte/icons/dist/56/message_read_outline';

export default class RouteDialogPanel extends React.Component {
  
  routeDialog = () => {
    let res = {}
    
    for (let [key, value] of this.props.hashParams) {
      res[key] = value
    }
    this.props.sendResponse(res)
    return true;
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader/>
        <Gradient>
          <StandardRequestAccess 
            hashParams={hashParams} 
            requests="Перенаправление к диалогу с чат-ботом"
            icon={<Icon56MessageReadOutline/>}
            action={<Link href={'https://vk.com/im?sel=-' + hashParams.get('bot_id')} onClick={this.routeDialog} target="_blank"><Button size="xl">Быстрый диалог</Button></Link>}
          />
        </Gradient>
      </Panel>
    );
  }
}