import React from 'react';

import {
  PanelHeader,
  Search,
  PanelHeaderButton
} from '@vkontakte/vkui';

import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';

import bridge from '@vkontakte/vk-bridge';


import './css/standardSearchHeader.css';

export default ({ onChange, onClose, closeIcon, key, title, setSearch}) => {
  let hashParams = window.location.search
  hashParams = new URLSearchParams(hashParams);

  const isDesktopWebPlatfrom = hashParams.get('vk_platform') === 'desktop_web';
  console.log(hashParams.get('vk_platform'))
  return (<PanelHeader key={key} left={<PanelHeaderButton onClick={(e) => {
    if (onClose) {
      onClose(e)
    } else {

      bridge.send("VKWebAppClose", {
        "status": "success", 
        "payload": {}
      });

      window.history.back();
    }
  }}>{closeIcon ? closeIcon : <Icon24Cancel/>}</PanelHeaderButton>}>
    {setSearch ? <Search onChange={onChange} className={!isDesktopWebPlatfrom ? "margins" : ""}/> : title ? title : "ChatBot Tools"}
  </PanelHeader>);
}
