import React from 'react';

import {
  Panel
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import bridge from '@vkontakte/vk-bridge';

export default class JoinGroupPanel extends React.Component {
  
  joinGroup = () => {
    let {hashParams} = this.props
    
    if (bridge.supports("VKWebAppJoinGroup")) {
      bridge.send("VKWebAppJoinGroup", {
        "group_id": Number(hashParams.get('group_id'))
      }).then(res => {
        this.props.sendResponse(res);
      }).catch(e => {
        this.props.sendError("Не удалось вступить в сообщество", e)
      });
    } else {
      this.props.sendError("Вступление в сообщество на этом устройстве не доступно", {
        error_code: "not_supports"
      })
    }
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader/>
        <StandardRequestAccess 
          onClick={this.joinGroup} 
          hashParams={hashParams} 
          requests="Предлагает вступить в сообщество"
          actionText="Вступить"
        />
      </Panel>
    );
  }
}