import React from 'react';

import {
  Panel
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';

import bridge from '@vkontakte/vk-bridge';

export default class ReadQRCodePanel extends React.Component {
  
  componentWillMount () {
    if (bridge.supports("VKWebAppOpenCodeReader")) {
      bridge.send("VKWebAppOpenCodeReader", {}).then(res => {
        this.props.sendResponse(res)
      }).catch(e => {
        this.props.sendError("QR не удалось прочитать", e)
      })
    } else {
      this.props.sendError("Чтение QR не поддерживается", {
        error_code: "not_supports"
      })
    }
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader/>
        Открываем сканер ...
      </Panel>
    );
  }
}