import React from 'react';

import {
  CardGrid,
  Placeholder,
  Avatar,
  Button
} from '@vkontakte/vkui';

import Icon56LockOutline from '@vkontakte/icons/dist/56/lock_outline';

export default ({hashParams, requests, onClick, actionText, action, icon}) => {
  let botName = hashParams.get('bot_name') || "Чат-бот"
  let botPhoto = hashParams.get('bot_photo') || "";

  return (
  <CardGrid>
    <Placeholder
      icon={botPhoto ? <Avatar size={82} src={botPhoto}/> : icon ? icon : <Icon56LockOutline/>}
      header={botName}
      action={action ? action : <Button size="xl" onClick={onClick}>{actionText ? actionText : "Разрешить"}</Button>}
    >{requests}</Placeholder>
  </CardGrid>);
};