import React from 'react';

import {
  Panel
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import bridge from '@vkontakte/vk-bridge';

export default class GetAppVersionPanel extends React.Component {
  
  componentWillMount () {
    if (bridge.supports("VKWebAppGetClientVersion")) {
      bridge.send("VKWebAppGetClientVersion", {}).then(res => {
        this.props.sendResponse(res)
      }).catch(e => {
        this.props.sendError("Не удалось получить ифнормацию о приложении", e)
      });
    } else {
      this.props.sendError("Получение информации о версии приложения не доступно на этом устройстве", {
        error_code: "not_supports"
      })
    }
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader/>
        Получаем версию ...
      </Panel>
    );
  }
}