import React from 'react';

import {
  Panel,
  CardGrid,
  Card,
  Placeholder,
  Button,
  Avatar
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import bridge from '@vkontakte/vk-bridge';

export default class GetEmailPanel extends React.Component {
  
  getEmail = () => {
    if (bridge.supports("VKWebAppGetEmail")) {
      bridge.send("VKWebAppGetEmail", {}).then(res => {
        this.props.sendResponse({
          email: res.email
        })
      }).catch(e => {
        this.props.sendError("Не удалось получить Email", e)
      });
    } else {
      this.props.sendError("Получение Email на этом устройстве не доступно", {
        error_code: "not_supports"
      })
    }
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader/>
        <StandardRequestAccess 
          onClick={this.getEmail} 
          hashParams={hashParams} 
          requests="Запрашивает доступ к Вашему Email адресу"
        />
      </Panel>
    );
  }
}